.line_through {
  position: relative;
  font-size: 30px;
  z-index: 1;
  overflow: hidden;
  text-align: center; 
}
.line_through:before, .line_through:after {
  position: absolute;
  top: 51%;
  overflow: hidden;
  width: 50%;
  height: 1px;
  content: '\A0';
  background-color: #ccc;

}
.line_through:before {
  margin-left: -50%;
  text-align: right;

}
.side_content_card{
  width: calc(100% - 250px);
